body{font-family:'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.jumbotron-top{background:#4CC3D9;background:-webkit-linear-gradient(45deg, rgba(76,195,217,0) 0%, #98e3ea 100%);background:linear-gradient(45deg, rgba(76,195,217,0) 0%, #98e3ea 100%);margin-bottom:0;min-height:100vh;position:relative}.jumbotron-top .container{position:absolute;height:600px;left:0;right:0;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);text-align:center}h1{display:inline-block;font-family:'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;font-weight:300;line-height:1;padding-right:80px;background-position:center right;background-repeat:no-repeat;background-size:55px auto;background-image:url(data:image/png;base64,)}.list-vue{text-align:left;margin:20px auto;padding:0}p.lead{font-size:1.8em;margin:1em 0}.btn-outline{background:none;padding:10px 16px;font-size:18px;line-height:1.3333333;border-radius:6px;border:3px solid #91ddec;color:#147688}.btn-outline:hover{border-color:#41b883;background-color:rgba(65,184,131,0.67)}.btn-outline:active,.btn-outline:focus{border-color:#41b883;background-color:#41b883}.down-arrow{position:absolute;bottom:10px;width:100%;text-align:center;color:rgba(0,0,0,0.5);font-size:1.6em}.down-arrow i{font-size:.7em;opacity:.4}#v-select{max-width:500px;margin:0 auto}#v-select .dropdown-toggle{background:#fff;border-color:rgba(82,166,183,0.39)}#v-select .selected-tag{color:#147688;background-color:#d7f3f9;border-color:#91ddec}#v-select .selected-tag .close{color:#147688;opacity:.5}#v-select.dropdown.open .dropdown-toggle,#v-select.dropdown.open .dropdown-menu,#v-select.dropdown.open .open-indicator:before{border-color:#4CC3D9}#v-select .active a{background:rgba(50,50,50,0.1);color:#333}#v-select.dropdown .highlight a,#v-select.dropdown li:hover a{background:#4CC3D9;color:#fff}.selected-tag .close{font-family:"Helvetica Neue", "Helvetica";font-weight:400}



